@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

* {
  box-sizing: border-box;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  outline: none;
  font-family: 'Inter', sans-serif;
}

body {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  min-height: 100vh;
  position: relative;
  background: #f3f5f7;
  max-width: 100%;
  flex-wrap: wrap;

  main {
    flex: 0 1 100%;
    padding-left: 110px;
    max-width: calc(100%);
  }

  section {
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    min-height: calc(100% - 126px);
  }

  .container {
    flex: 0 1 100%;
    padding: 60px;
  }

  &.active {
    height: 100vh;
    overflow: hidden;
  }
}

header {
  width: 100%;
  height: 126px;
  border-bottom: 1px solid #e9e9e9;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;

  .progress {
    flex: 0 1 100%;
  }

  .login {
    flex: 0 0 490px;
    padding: 0 50px 0 69px;
    border-left: 1px solid #e9e9e9;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__user {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 14px;
      text-decoration: none;

      &--avatar {
        flex: 0 0 40px;
        width: 40px;
        height: 40px;
        border-radius: 40px;
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        color: #90949A;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        &:before {
          content: '';
          top: 0;
          left: 0;
          position: absolute;
          width: 37px;
          height: 37px;
          border-radius: 40px;
          border: 1.5px dashed #A9AEB2;
          transition: all .3s linear;
        }
      }

      &:hover {
        color: #e8b98b;

        .login__user--avatar:before {
          border: 1.5px dashed #E8B98B;
          transform: rotate(18deg);
        }
      }

      p {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        color: #90949A;
      }
    }

    &__btn {
      a {
        display: inline-block;
        width: 40px;
        height: 40px;
        flex: 0 0 40px;
        background: url('../images/logout.svg') center no-repeat;
      }
    }
  }
}

aside {
  flex: 0 0 110px;
  width: 110px;
  height: 100vh;
  background: linear-gradient(171.27deg, #27272C 10.95%, #171E28 142.43%);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 70px;
  z-index: 88;

  .logo {
    height: 126px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #3c3c3c;
    width: 100%;
  }

  .xs,
  .nav {
    display: none;
  }
}

nav {
  flex: 0 0 490px;
  border-left: 1px solid #e9e9e9;
  position: relative;
  background: #fff;
  position: sticky;
  top: -10px;
  max-height: 100vh;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    padding-top: 40px;

    li {
      width: 100%;
    }

    a {
      width: 100%;
      display: inline-block;
      padding: 23px 50px 23px 100px;
      text-decoration: none;
      background-image: url('../images/lock.svg');
      background-position: 63px center;
      background-repeat: no-repeat;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 0%;
        transition: all 0s linear;
        z-index: -1;
        background: linear-gradient(-90deg, #F3F5F7 -3.53%, rgba(243, 245, 247, 0) 102.03%);
      }

      &:after {
        content: '';
        width: 5px;
        right: 0;
        top: 50%;
        height: 0%;
        transform: translateY(-50%);
        background: #e8b98b;
        position: absolute;

      }

      &:hover {
        background: url('../images/lock.svg') 63px center no-repeat;

        &:before {
          width: 100%;
          transition: all 0.7s linear 0.2s;
        }

        &:after {
          // width: 5px;
          height: 100%;
          transition: all 0.3s linear;
        }

        p {
          color: #232323;
        }
      }

      &.done {
        background-image: url('../images/done.svg');

        &:hover {
          background: url('../images/done.svg') 63px center no-repeat;
        }
      }

      &.current {
        background-image: url('../images/current.svg');

        &:hover {
          background: url('../images/current.svg') 63px center no-repeat;
        }
      }
    }

    span {
      display: inline-block;
      margin: 0 0 4px;
      font-size: 12px;
      line-height: 140%;
      text-transform: uppercase;
      color: #A9AEB2;
      position: relative;
      z-index: 2;
    }

    p {
      margin: 0;
      font-size: 18px;
      line-height: 130%;
      color: #707378;
      position: relative;
      z-index: 2;
    }
  }

  .collapse {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 32px;
    height: 64px;
    border-radius: 0 32px 32px 0;
    background: #ededed url('../images/nav.svg') 30% center no-repeat;
    z-index: 8;

    &:hover {
      background: #E8B98B url('../images/nav-h.svg') 30% center no-repeat;
    }
  }

  &.float {
    display: none;
  }
}

.login {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: nowrap;

  &__image {
    flex: 0 0 50%;
    background: linear-gradient(171.27deg, #27272C 10.95%, #171E28 142.43%);
    border-left: 1px solid #3C3D3F;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    color: #fff;
    padding: 120px 70px;
    position: relative;
    overflow: hidden;

    span {
      display: inline-block;
      margin: 0 0 16px;
      font-weight: normal;
      font-size: 16px;
      line-height: 140%;
      color: #9C9C9C;
      position: relative;
      z-index: 3;
    }

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 46px;
      line-height: 130%;
      color: #FFFFFF;
      margin: 0 0 40px;
      position: relative;
      z-index: 3;
    }

    p {
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      color: #E2E2E2;
      margin: 0;
      position: relative;
      z-index: 3;
    }

    &--image {
      .sphere {
        position: absolute;
        border-radius: 500px;
        background: linear-gradient(184.09deg, #37363C -49.72%, #171E28 153.3%);
        z-index: 1;
        transition: all 1s linear;

        &--big {
          // animation: sbig 12s ease infinite;
          width: 505px;
          height: 505px;
          top: 240px;
          left: -70px;
        }

        &--small {
          // animation: ssmall 14s ease infinite;
          width: 175px;
          height: 175px;
          top: 560px;
          right: -20px;
        }
      }

      img {
        position: absolute;
        z-index: 2;
        top: 130px;
        left: -90px;
        transition: all 1s linear;
      }
    }
  }

  &__form {
    flex: 0 0 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    form {
      max-width: 500px;

      .btn {
        width: 100%;
        margin-top: 20px;
      }

      .btn__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .btn {
          width: auto;
          padding: 16px 65px;
        }

        a {
          font-weight: normal;
          font-size: 18px;
          line-height: 140%;
          text-decoration-line: underline;
          color: #90949A;
          margin-top: 20px;
        }
      }

      h2 {
        font-style: normal;
        font-weight: 600;
        font-size: 32px;
        line-height: 140%;
        color: #000000;
        margin: 0 0 40px;
      }

      label {
        width: 100%;
        margin: 0 0 15px;
        display: inline-block;
        position: relative;

        [type="text"]+i {
          position: absolute;
          top: 50%;
          margin-top: -12px;
          right: 24px;
          width: 24px;
          height: 24px;
          background: url('../images/eye-close.svg') center no-repeat;
          cursor: pointer;
        }

        [type="password"]+i {
          position: absolute;
          top: 50%;
          margin-top: -12px;
          right: 24px;
          width: 24px;
          height: 24px;
          background: url('../images/eye.svg') center no-repeat;
          cursor: pointer;
        }

        .error__text {
          font-size: 14px;
          line-height: 140%;
          color: #ee987f;
          margin: 10px 0 0;
          width: 100%;
          display: none;
        }

        .error+.error__text {
          display: inline-block;
        }

        .error {
          border: 1px solid #EB5757;
        }
      }

      [type="text"],
      [type="password"] {
        background: #F9F9F9;
        border-radius: 10px;
        height: 60px;
        padding: 0 24px;
        border: none;
        width: 100%;
        font-size: 16px;
        border: 1px solid transparent;
      }

      [type="checkbox"] {
        display: none;

        &:checked+p {
          background: url('../images/check-active.svg') left center no-repeat;
          color: #666;
        }

        &+p {
          font-weight: normal;
          cursor: pointer;
          font-size: 16px;
          line-height: 24px;
          color: #90949A;
          padding-left: 38px;
          text-align: left;
          background: url('../images/check.svg') left center no-repeat;

          &:hover {
            color: #666;
          }

          a {
            color: #e8b98b;
            text-decoration: none;
          }
        }
      }

      hr {
        margin: 24px 0;
        border: 1px solid #E0E0E0;
      }

      p {
        width: 100%;
        // text-align: center;
        font-weight: normal;
        cursor: pointer;
        font-size: 16px;
        line-height: 24px;
        color: #90949A;

        &.text-dark {
          color: #333;
          margin-top: -20px;
          margin-bottom: 40px;
        }

        a {
          color: #e8b98b;
          text-decoration: none;
        }
      }
    }
  }
}

.btn {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  padding: 16px 46px;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.4;
  border: none;
  cursor: pointer;

  span {
    position: relative;
    z-index: 2;
  }

  &-primary {
    color: #fff;
    background: #e8b98b;
    overflow: hidden;
    position: relative;
    // transition: all 1.2s linear;

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      background: linear-gradient(220deg, #E8B98B 8%, #E2B182 40%, #EBCBAB 60%, #E2B182 80%, #E8B98B 100%);
      position: absolute;
      top: 0;
      left: -100%;
      transition: all 0s linear;
      filter: blur(6px)
    }

    &:hover {
      box-shadow: 0px 5px 18px -8px rgba(0, 0, 0, 0.4);
    }

    &:hover:before {
      left: 100%;
      transition: all 1s linear;
    }
  }

  &-secondary {
    color: #fff;
    background: #3A394B;
    overflow: hidden;
    position: relative;
    padding: 20px 46px;
    // transition: all 1.2s linear;

    &:before {
      content: '';
      width: 102%;
      height: 102%;
      background: linear-gradient(220deg, #E8B98B 8%, #E2B182 40%, #EBCBAB 60%, #E2B182 80%, #E8B98B 100%);
      background: linear-gradient(220deg, #3A394B 8%, #353445 40%, #4A4869 60%, #333243 80%, #3A394B 100%);
      position: absolute;
      top: 0;
      left: -105%;
      transition: all 0s linear;
      filter: blur(6px)
    }

    &:hover {
      box-shadow: 0px 5px 18px -8px rgba(0, 0, 0, 0.4);
    }

    &:hover:before {
      left: 100%;
      transition: all 1s linear;
    }
  }

  &[disabled="disabled"] {
    filter: grayscale(10);
    cursor: default;
  }
}

.progress {
  &__bar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 16px;

    &--title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      strong {
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
        color: #707378;
      }

      p {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        text-align: right;
        color: #90949A;
        margin: 0;
      }
    }
  }

  &__text {
    display: inline-block;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    text-transform: uppercase;
    color: #A9AEB2;
    margin: 0 0 8px;
  }

  &__meter {
    width: 100%;
    background: #d3d3d3;
    height: 9px;
    border-radius: 9px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &--bar {
      position: absolute;
      top: 0;
      left: 0;
      height: 9px;
      border-radius: 9px;
      background: #e8b98b;
    }

    span {
      width: 9px;
      height: 9px;
      background: #272727;
      border-radius: 9px;
      position: relative;
      z-index: 2;
    }
  }
}

section .container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  max-width: 100%;
  max-width: calc(100% - 490px);
}

.title {
  background: #fff;
  padding: 32px;
  border-radius: 8px;
  margin: 0 0 20px;

  span {
    display: inline-block;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    text-transform: uppercase;
    color: #A9AEB2;
    margin: 0 0 8px;
  }

  h1 {
    margin: 0 0 16px;
    font-weight: 600;
    font-size: 32px;
    line-height: 39px;
    color: #191D26;
  }

  p {
    margin: 0;
    font-size: 16px;
    line-height: 140%;
    color: #6D7787;
  }
}

.video {
  width: 100%;
  margin: 0 0 36px;

  video {
    width: 100%;
  }
}

.material {
  margin: 0 0 60px;

  h2 {
    font-weight: 600;
    font-size: 32px;
    line-height: 39px;
    color: #191D26;
    margin: 0 0 24px;
  }

  &__grid {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 10px;
    flex-wrap: wrap;

    &--item {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 16px;
      border-radius: 8px;
      background: #fff;
      flex: 0 0 calc(25% - 7.5px);
      gap: 16px;
      text-decoration: none;
      // transition: all .2s linear;
      overflow: hidden;
      position: relative;

      * {
        position: relative;
        z-index: 2;
      }

      p {
        font-weight: normal;
        font-size: 16px;
        line-height: 140%;
        color: #494C55;
        overflow: hidden;
        max-width: 90%;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 0 0 4px;
        // transition: all .2s linear;
      }

      span {
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #191D26;
        opacity: 0.5;
        display: inline-block;
        overflow: hidden;
        max-width: 90%;
        text-overflow: ellipsis;
        white-space: nowrap;
        // transition: all .2s linear;
      }

      .text {
        width: calc(100% - 40px);
      }

      .image {
        background: linear-gradient(180.45deg, #4D4A51 -16.34%, #221F26 81.55%);
        width: 40px;
        height: 40px;
        flex: 0 0 40px;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &:before {
        content: '';
        width: 100%;
        height: 100%;
        background: linear-gradient(220deg, #E8B98B 8%, #E2B182 40%, #EBCBAB 60%, #E2B182 80%, #E8B98B 100%);
        position: absolute;
        top: 0;
        left: -110%;
        transition: all 0s linear;
        filter: blur(6px)
      }

      &:hover:before {
        left: 100%;
        transition: all 1s linear;
      }

      &:hover {
        background: #e8b98b;
        box-shadow: 0px 10px 15px -10px rgba(0, 0, 0, 0.2);

        p,
        span {
          color: #fff;
        }
      }
    }
  }
}

.quiz {
  margin: 0 0 48px;

  &__wrapper {
    background: #fff;
    border-radius: 8px;
  }

  &__start {
    margin: 0 0 50px;
    border-radius: 8px;
    background: #2b2b2b;

    &--header {
      padding: 38px 40px 30px;
      border-bottom: 1px solid #383838;

      h3 {
        color: #fff;
        font-size: 24px;
        margin: 0;
      }
    }

    &--body {
      padding: 40px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    &--info {
      display: flex;
      justify-content: flex-start;
      color: #fff;
      text-align: center;
      gap: 60px;

      strong {
        font-weight: 600;
        font-size: 20px;
        line-height: 120%;
        color: #FFFFFF;
        display: inline-block;
        margin: 0 0 4px;
        position: relative;
      }

      p {
        font-weight: normal;
        font-size: 14px;
        line-height: 120%;
        color: #A8A8A8;
        margin: 0;
      }

      i {
        width: 14px;
        height: 14px;
        border-radius: 13px;
        border: 1px solid #a8a8a8;
        display: flex;
        align-items: center;
        justify-content: center;
        font-style: normal;
        font-size: 8px;
        font-weight: 500;
        position: absolute;
        top: -14px;
        right: -14px;
        cursor: help;
      }
    }

    &--btn {
      position: relative;
    }
  }

  &__modal {
    position: fixed;
    background: #e8b98b;
    border-radius: 8px;
    width: 190px;
    max-height: 50px;
    z-index: -1;
    overflow: hidden;
    transition: all 0s ease-out 0s;
    top: -50px;
    left: 50%;
    transform: translateY(-50%);
    opacity: 0;

    .close {
      position: absolute;
      top: 20px;
      color: #777;
      font-weight: 100;
      font-family: monospace;
      right: 30px;
      font-size: 36px;
      cursor: pointer;
    }

    &--wrapper {
      display: none;

      &.active {
        display: inline-block;
        width: 100%;
      }
    }

    &--wrapper,
    .close {
      opacity: 0;
      transition: opacity 0s ease-in-out;
    }

    &--header {
      padding: 40px 50px 0px;
      border-bottom: 1px solid #F3F5F7;
      width: 100%;
      min-width: 820px;

      span {
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        text-transform: uppercase;
        color: #A9AEB2;
        display: inline-block;
        margin: 0 0 8px;
      }

      h3 {
        margin: 0;
        color: #707070;
        margin: 0 0 30px;
      }

      img {
        height: 300px;
        width: 100%;
        object-fit: cover;
        margin: 0 0 30px;
      }
    }

    &--body {
      padding: 30px 50px 50px;

      &.text {
        p {
          font-size: 16px;
          color: #333;
          line-height: 1.4;
          margin: 0 0 10px;
        }
      }
    }

    &--overlay {
      position: fixed;
      background: transparent;
      z-index: -1;
      opacity: 0;
      transition: none;
      top: 50px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 100vh;
      overflow: auto;

      &.active {
        z-index: 101;
        top: 0;
        background: rgba(0, 0, 0, 0.4);
        opacity: 1;
        transition: background .4s linear .0s;
      }

      &.active .quiz__modal {
        top: 50px;
        left: 50%;
        width: 100%;
        max-width: 920px;
        z-index: 9;
        opacity: 1;
        max-height: 1920px;
        background: #fff;
        transform: translate(-50%, 0%);
        position: absolute;
        transition:
          background 0.3s ease-in-out 0s,
          top 0.4s ease-in-out,
          left 0.4s ease-in-out,
          width 0.7s ease-in-out 0.1s,
          max-height .7s ease-in-out 0.1s;

        .close {
          opacity: 1;
          transition: opacity 0.5s ease-in-out 0.5s;
        }

        .quiz__modal--wrapper {
          opacity: 1;
          transition: opacity 0.5s ease-in-out 0.5s;
        }
      }
    }
  }

  &__form {
    fieldset {
      border: none;
      padding: 0;
      margin: 0 0 30px;
    }

    [type="text"] {
      width: 100%;
      height: 64px;
      padding: 0 24px;
      background: #f9f9f9;
      border-radius: 10px;
      border: none;
      margin: 0;
    }

    & &--text {
      margin: 0 0 15px;

      label {
        display: inline-block;
        width: 90%;
      }
    }

    & &--checkbox {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;

      label {
        display: inline-block;
        width: 100%;
        margin: 0 0 15px;

        input {
          display: none;

          &:checked+p {
