  margin: 0 0 36px;
}

.lessons {
  width: 100%;
  margin-bottom: 20px;

  .slick-list {
    padding: 0 70px;

    &:before {
      content: '';
      height: 100%;
      left: 0;
      top: 0;
      width: 70px;
      background: linear-gradient(90deg, #F3F5F7 0%, rgba(196, 196, 196, 0) 100%);
      position: absolute;
      z-index: 7;
    }

    &:after {
      content: '';
      height: 100%;
      right: 0;
      top: 0;
      width: 70px;
      background: linear-gradient(270deg, #F3F5F7 0%, rgba(196, 196, 196, 0) 100%);
      position: absolute;
      z-index: 7;
    }
  }

  .slick-prev {
    left: 0;
    z-index: 8;
    width: 44px;
    height: 44px;
    background: url('../images/slick-arrow.svg') center no-repeat;

    &:before {
      display: none;
    }
  }

  .slick-next {
    right: 0;
    z-index: 8;
    width: 44px;
    height: 44px;
    background: url('../images/slick-arrow.svg') center no-repeat;
    transform: rotate(180deg) translateY(50%);

    &:before {
      display: none;
    }
  }

  &__slick {
    &-item {
      height: 130px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      border-radius: 12px;
      margin: 0 5px;
      position: relative;
      border: 3px solid #f3f5f7;

      &.current {
        border: 3px solid #E8B98B;
        border-radius: 8px;
      }

      &.lock {
        position: relative;

        &:before {
          content: url('../images/lock-gold.svg');
          position: absolute;
          top: 20px;
          right: 20px;
          background: #fff;
          border-radius: 20px;
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 8;
        }

        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background: rgba(255, 255, 255, .7);
        }
      }

      &>div {
        position: absolute;
        bottom: 20px;
        left: 20px;
        color: #fff;
      }

      span {
        font-size: 10px;
        text-transform: uppercase;
      }

      p {
        font-size: 14px;
        margin: 0;
      }
    }
  }
}

